export const DEFAULT_FIlTERS = {
    regex: [],
    type: new Set(["dom", "fragment", "root", "hoc", "textSignal"]),
};
export function parseFilters(raw) {
    const type = new Set();
    if (raw.type.fragment)
        type.add("fragment");
    if (raw.type.dom)
        type.add("dom");
    if (raw.type.hoc)
        type.add("hoc");
    if (raw.type.root)
        type.add("root");
    if (raw.type.textSignal)
        type.add("textSignal");
    return {
        regex: raw.regex.filter(x => x.enabled).map(x => new RegExp(x.value, "gi")),
        type,
    };
}
