export function storeTime(timings, id, time) {
    timings.set(id, time);
}
export function getTime(timings, id) {
    return timings.get(id) || 0;
}
export function removeTime(timings, id) {
    timings.start.delete(id);
    timings.end.delete(id);
}
export function createVNodeTimings() {
    return {
        start: new Map(),
        end: new Map(),
    };
}
